import React, { Component } from 'react';

import Stopwatch from './components/DurationExercise'
import Counter from './components/RepetitionExercise'
import Countdown from './components/Countdown'
import ImageSlider from './components/ImageSlider';


class App extends Component {

  state = {
    visible: true,
    componentShown: "Counter"
  };

  render() {
    if(this.state.componentShown === "Counter") {
      return(
        <div className="App">
          <h1>Welcome to the repition exercise</h1>
          <h2>Jumping Jacks</h2>
          <Counter />
          <h2>Burpees</h2>
          <Counter />
          <h2>Squats</h2>
          <Counter />
          <hr />
          <button
          onClick={() => {
            this.setState({ componentShown: "Stopwatch"});
          }}
          >
            Go to duration exercise
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "Countdown"});
          }}
          >
            Go to the countdown
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "ImageSlider"});
          }}
          >
            Go to the Image Slider
          </button>
        </div>
      );
    } else if (this.state.componentShown === "Stopwatch") {
      return(
        <div className="App">
          <h1>Welcome to the duration exercise</h1>
          <h2>The Mile</h2>
          <Stopwatch />
          <h2>Swimming</h2>
          <Stopwatch />
          <h2>Cycling</h2>
          <Stopwatch />
          <hr />
          <button
          onClick={() => {
            this.setState({ componentShown: "Counter"});
          }}
          >
            Go to repitition exercise
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "Countdown"});
          }}
          >
            Go to the countdown
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "ImageSlider"});
          }}
          >
            Go to the Image Slider
          </button>
        </div>
      );
    } else if (this.state.componentShown === "Countdown") {
      return(
        <div className="App">
          <Countdown />
          <hr />
          <button
          onClick={() => {
            this.setState({ componentShown: "Counter"});
          }}
          >
            Go to the Counter
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "Stopwatch"});
          }}
          >
            Go to the Stopwatch
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "ImageSlider"});
          }}
          >
            Go to the Image Slider
          </button>
        </div>
      );
    } else if (this.state.componentShown === "ImageSlider") {
      return(
        <div className="App">
          <ImageSlider />
          <hr />
          <button
          onClick={() => {
            this.setState({ componentShown: "Counter"});
          }}
          >
            Go to the Counter
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "Stopwatch"});
          }}
          >
            Go to the Stopwatch
          </button>
          <button
          onClick={() => {
            this.setState({ componentShown: "Countdown"});
          }}
          >
            Go to the Countdown
          </button>
        </div>
      );

    return null;
    }
  }
}

export default App;
