import React, {Component} from "react";

export default class ImageSlider extends React.Component {
  state = {
    images: [
      "https://i.picsum.photos/id/237/200/300.jpg?hmac=TmmQSbShHz9CdQm0NkEjx1Dyh_Y984R9LpNrpvH2D_U",
      "https://i.picsum.photos/id/866/200/300.jpg?hmac=rcadCENKh4rD6MAp6V_ma-AyWv641M4iiOpe1RyFHeI",
      "https://i.picsum.photos/id/61/200/300.jpg?grayscale&hmac=4oR0xhApjCw_YO68_zxzspKZ11oBKhzpBYNnJ-oBIQ4",
      "https://i.picsum.photos/id/451/200/300.jpg?blur=2&hmac=VoHp-bFfMmeGXzJ92q2gWW4-JjRslYfx3XLd9JJi2Kk"
    ],
    idx: 0
  };

  handleNext = () => {
    this.setState({
      idx: this.state.idx + 1
    });
  };

  render() {
    return (
      <div>
        <button
          onClick={() => {
            this.setState({
              idx: this.state.idx - 1
            });
          }}
        >
          previous
        </button>
        <img
          style={{
            width: 100,
            height: 100
          }}
          src={this.state.images[this.state.idx]}
        />
        <button onClick={this.handleNext}>next</button>
      </div>
    );
  }
}